.flex {
    display: flex;
    margin: 40px 25px;
    justify-content: space-around;

}

@media (min-width: 768px) and (max-width: 1000px){
    .flex{
        margin: 10px 5px;
    }
}
@media  (max-width: 767px){
    .flex{
        margin: 10px 5px;
        flex-direction: column;
    }
}